body {
    background-image: url("data:image/svg+xml;base64,");
    overflow-x: hidden;

    @media (min-width: 1000px) {
      border-right: 1px solid var(--grey-dark);
      border-left: 1px solid var(--grey-dark);
      margin-inline: var(--space-l);
      padding-inline: 0;
    }
  }

  p {
    max-width: 65ch;
    padding: var(--space-3xs);
  }

  a {
    pointer-events: all;
  }

  @media print {
    h1 {
      color: black;
    }
    
    body {
      background: white;
      color: black;
      font-size: 12px;
    }
  
    a::after {
      content: " (" attr(href) ") ";
    }
  
    a[class] {
      color: black;
      background: white;
    }
  
    nav {
      display: none;
    }
  }